// Options
//
// Quickly modify global styling by enabling or disabling optional features.

Rsenable-caret:                true !default;
Rsenable-rounded:              true !default;
Rsenable-shadows:              false !default;
Rsenable-gradients:            false !default;
Rsenable-transitions:          true !default;
Rsenable-reduced-motion:       true !default;
Rsenable-smooth-scroll:        true !default;
