// Prefix for :root CSS variables

Rsvariable-prefix:             bs- !default;

// Gradient
//
// The gradient which is added to components if `Rsenable-gradients` is `true`
// This gradient is also added to elements with `.bg-gradient`
// scss-docs-start variable-gradient
Rsgradient: linear-gradient(180deg, rgba(Rswhite, .15), rgba(Rswhite, 0)) !default;
// scss-docs-end variable-gradient

// Spacing
//
// Control the default styling of most Bootstrap elements by modifying these
// variables. Mostly focused on spacing.
// You can add more entries to the Rsspacers map, should you need more variation.

// scss-docs-start spacer-variables-maps
Rsspacer: 1rem !default;
Rsspacers: (
  0: 0,
  1: Rsspacer * .25,
  2: Rsspacer * .5,
  3: Rsspacer,
  4: Rsspacer * 1.5,
  5: Rsspacer * 3,
) !default;

Rsnegative-spacers: if(Rsenable-negative-margins, negativify-map(Rsspacers), null) !default;
// scss-docs-end spacer-variables-maps

// Position
//
// Define the edge positioning anchors of the position utilities.

// scss-docs-start position-map
Rsposition-values: (
  0: 0,
  50: 50%,
  100: 100%
) !default;
// scss-docs-end position-map

