  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px
) !default;
// scss-docs-end grid-breakpoints

@include _assert-ascending(Rsgrid-breakpoints, "Rsgrid-breakpoints");
@include _assert-starts-at-zero(Rsgrid-breakpoints, "Rsgrid-breakpoints");


